::-webkit-scrollbar {
  display: none;
}

html {
  scrollbar-width: none;
}

body {
  -ms-overflow-style: none;
}


body {
  margin: 0;
  font-family: 'Pretendard';
}

div,
nav {
  box-sizing: border-box;
}

p,
h1,
h2,
h3,
h4,
h5 {
  margin: 10px;
}

p {
  font-weight: 400;
}

h1 {
  font-weight: 600;

  @media (max-width: 600px) {
    font-size: 25px;
  }
}

.section {
  width: 100%;
  height: auto;
  padding-top: 60px;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  width: 100%;
  height: auto;
}

.content-container {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: start;
}

.blank-container {
  width: 100%;
  height: 30px;
}

.underline {
  width: 130px;
  height: 4px;
  background: black;
  margin: 0 0 30px 0;
  border-radius: 5px;

  @media (max-width: 600px) {
    width: 100px;
  }
}

#whitesmoke {
  background-color: whitesmoke;
}

